header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 5; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    @media (max-width: 1200px) {
      display: block; }
    &_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;
        @media (max-width: 1200px) {
          font-size: 11px; }
        &:hover {
            text-decoration: none;
            color: #1eacc7; } }
    &_item {
          border-right: 2px dotted black;
          padding-right: 28px;
          &:last-child {
              border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  .subheader_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    &:hover {
        background: #0056b3;
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase; } } }

.promo {
  min-height: 800px;
  background: url("/img/bg.png") center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  .promo_subheader {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    &:hover {
        background: #ff5656;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        transition: 0.4s all; } } }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.subtitle {
  margin-top: 19px;
  font-size: 17px;
  font-weight: 400;
  text-align: center; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.mobile {

  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url("/img/bg/mobilebg.png") center (center / cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 240px;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url("../img/bg/mb 1.png") center (center / cover) no-repeat; }
    &_2 {
      background: url("../img/bg/mb 2.png") center (center / cover) no-repeat; }
    &_3 {
      background: url("../img/bg/mb 3.png") center (center / cover) no-repeat; }
    &_4 {
      background: url("../img/bg/mb 4.png") center (center / cover) no-repeat; }
    &_5 {
      background: url("../img/bg/mb 5.png") center (center / cover) no-repeat; }
    &_6 {
      background: url("../img/bg/mb 6.png") center (center / cover) no-repeat; }
    &_subtitle {
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      line-height: 31px;
      text-transform: uppercase;
      text-align: center; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      width: 26px;
      height: 26px;
      background-color: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer;
      bottom: 36px;
      left: 50%;
      transform: translateX(-50%);
      &:before {
        content: "";
        display: block;
        width: 14px;
        height: 2px;
        background-color: #1eacc7;
        transition: 0.4s all; }
      &:after {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 2px;
        height: 14px;
        background-color: #1eacc7;
        transition: 0.4s all; }
      &:hover {
        &:before,&:after {
             background-color: fuchsia; } } } } }



.choice {
  position: relative;
  min-height: 895px;
  padding: 57px 0 138px 0;
  background: url('/img/bg/mobilebg.png') center (center / cover) no-repeat;

  &_img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto;
    z-index: 2; }
  &_descr {
    text-align: center;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px; } }


.require {
  padding: 68px 0 84px 0;
  &_block {
    margin: 33px 0;
    &_nmb {
      margin-bottom: 0; } }
  &_title {
    color: #1eacc7;
    font-size: 22px;
    font-weight: 400; }
  &_list {
    list-style-type: none;
    padding-left: 50px;
    margin-top: 22px;
    li {
      position: relative;
      font-size: 17px;
      font-weight: 300;
      line-height: 24px;
      margin-bottom: 10px;
      &:before {
        position: absolute;
        content: "";
        display:  block;
        left: -32px;
        top: 3px;
        width: 21px;
        height: 20px;
        background: url("../icon/layer 32.png")  center (center / cover) no-repeat; } } } }

.warning {
  .require_title {
    color: #ff5656; }
  .require_list {
    li {
      &:before {
        background: url("../icon/layer 31.png")  center (center / cover) no-repeat; } } } }

.end {
  position: relative;
  min-height: 717px;
  padding: 49px 0;
  background: url('/img/planet.png') center (center / cover) no-repeat;

  &_link {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 49px;
    text-align: center;
    font-size: 15px;
    font-weight: 400;
    line-height: 68px;
    text-transform: uppercase;
    color: #1fbad6;
    &:hover {
      text-decoration: none;
      color: #fff; } }

  h2 {
    color: #ffffff; } }
.subtitle_white {
  color: white; }

.footer {
  &_divider {
    width: 299px;
    height: 4px;
    background-color: #1fbad6;
    margin: 0 auto; }
  &_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 32px; }
  &_wrapper > div {
    width: 33%;
    &:nth-child(3) {
      display: flex;
      justify-content: flex-end; } }
  &_social {
    width: 160px;
    display: flex;
    justify-content: space-between; }
  &_item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 4px;
      width: 32px;
      border-radius: 100%;
      height: 32px;
      background-color: #070716;
      i {}
      color: white; }
  &_links {
    width: 390px;
    margin: 0 auto;
    &_main {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      a {
        color: #070716;
        font-size: 15px;
        font-weight: 300;
        text-transform: uppercase; } }
    &_sub {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      a {
        color: #999999;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase; } }
    &_lang {
      display: block;
      color: #1fbad6;
      font-size: 11px;
      font-weight: 400;
      margin-top: 19px;
      text-transform: uppercase;
      text-align: center; } } }
